<style scoped>
.export-item {
  margin: 20px 0;
}
</style>

<template>
  <section class="wrap">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <div class="export-list">
      <div class="export-item">
        <el-form :inline="true" :model="reservaForm">
          <el-form-item>
            <el-button type="text"></el-button>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker v-model="reservaForm.dateRange" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder="" align="left">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="reservaForm.state" placeholder="">
              <el-option v-for="state in reservaState" :key="state.id" :label="state.name" :value="state.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="reservaExport"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="export-item">
        <el-form :inline="true" :model="orderForm">
          <el-form-item>
            <el-button type="text"></el-button>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker v-model="orderForm.dateRange" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder="" align="left">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="orderForm.state" placeholder="">
              <el-option v-for="state in orderState" :key="state.id" :label="state.name" :value="state.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="orderExport"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="export-item">
        <el-form :inline="true" :model="masterForm">
          <el-form-item>
            <el-button type="text"></el-button>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker v-model="masterForm.dateRange" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder="" align="left">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="masterForm.state" placeholder="">
              <el-option v-for="state in masterState" :key="state.id" :label="state.name" :value="state.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="masterExport"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      reservaForm: {
        dateRange: "",
        start: "",
        end: "",
        state: ""
      },
      reservaState: [
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        },
        {
          id: 3,
          name: ""
        }
      ],

      orderForm: {
        dateRange: "",
        start: "",
        end: "",
        state: ""
      },
      orderState: [
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        },
        {
          id: 3,
          name: ""
        },
        {
          id: 4,
          name: ""
        }
      ],

      masterForm: {
        dateRange: "",
        start: "",
        end: "",
        state: ""
      },
      masterState: [
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        }
      ]
    };
  },

  methods: {
    reservaExport() {
      const getData = this.reservaForm;
      if (getData.dateRange !== "") {
        getData.start = new Date(getData.dateRange[0]).getTime();
        getData.end = new Date(getData.dateRange[1]).getTime();
      }
      let url = `${this.$api.host}output?table=reverse&start=${getData.start ||
        ""}&end=${getData.end || ""}&state=${getData.state}`;
      window.open(url);
    },

    orderExport() {
      const getData = this.orderForm;
      if (getData.dateRange !== "") {
        getData.start = new Date(getData.dateRange[0]).getTime();
        getData.end = new Date(getData.dateRange[1]).getTime();
      }
      let url = `${this.$api.host}output?table=reverse&start=${getData.start ||
        ""}&end=${getData.end || ""}&state=${getData.state}`;
      window.open(url);
    },
    masterExport() {
      const getData = this.masterForm;
      if (getData.dateRange !== "") {
        getData.start = new Date(getData.dateRange[0]).getTime();
        getData.end = new Date(getData.dateRange[1]).getTime();
      }
      let url = `${this.$api.host}output?table=reverse&start=${getData.start ||
        ""}&end=${getData.end || ""}&state=${getData.state}`;
      window.open(url);
    }
  }
};
</script>




